import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { Headers } from '@angular/http';
import { AuthServicesService } from './auth-services.service';
import { Router } from '@angular/router';
import { Observable, throwError} from "rxjs";
import { map, catchError } from 'rxjs/operators';
import { GetServicesService } from './get-services.service';
@Injectable({
  providedIn: 'root'
})
export class MessageService {

  _url:string = '/message';
  // _url:string = '/message';

  constructor(
    private _http : HttpClient,
    private _auth:AuthServicesService,
    private _getServices:GetServicesService

  ) { }

// post messages
  sendMessage(msg:any){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization': 'my-auth-token'
      })
    };
    return this._http.post(this._url, msg, httpOptions).pipe(
      catchError(this._getServices.handleError)
    );
  } // end post messages

// get messages
  getMessages(){
    return this._http.get(this._url).pipe(
      catchError(this._getServices.handleError)
    );
  }// end get messages

// remove messages
  removeMessage(id){
    return this._http.get(this._url+'/'+id+'/remove' ).pipe(
      catchError(this._getServices.handleError)
    );
  } // end remove messages

}
