import { Component, OnInit } from '@angular/core';
import { MessageService } from '../../services/message.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.scss']
})
export class MessagesComponent implements OnInit {

  constructor(
   private _messagesServices:MessageService,
   private _flashMessages:FlashMessagesService
  ) { }
  messages:any = 0;
  errorMessage:string;
  ngOnInit() {
// to get all meassges
    this._messagesServices.getMessages().subscribe((res:any)=>{
      if (res.success) {
        this.messages = res.messages;
      } else{
        this._flashMessages.show(res.errMSG, {cssClass: 'alert-danger', timeout: 3000})
      }
    }, 
      (err)=>{
        this.errorMessage = err;
      }
  )//end get all meassges
  }// end ngOnInit
// start remove message
  remove(id){
    this._messagesServices.removeMessage(id).subscribe((res:any)=>{
          for (let i = 0; i < this.messages.length; i++) {
            const item = this.messages[i];        
            if(item.id = id){
                this.messages.splice(i, 1);
                this._messagesServices.getMessages().subscribe((data:any)=>{
                  if (!res.success) {
                    this._flashMessages.show(res.err.message, {cssClass: 'alert-danger', timeout: 3000})
                  }else{
                    this.messages = data.messages;
                    this._flashMessages.show(res.MSG, {cssClass: 'alert-success', timeout: 3000})
                  }
                },
                  (err) =>{
                    this.errorMessage = err;
                  }
              ) // end get all messages aftar remove message
                break;
            }else{
              this._flashMessages.show(res.MSG, {cssClass: 'alert-success', timeout: 3000})
            }
      } // end for loop
    })
  } // end remove message
}
