import { Component, OnInit } from '@angular/core';
import { AuthServicesService } from '../../services/auth-services.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  username: string;
  password: string;
  errorMessage:string;

  constructor(
    private _authServices: AuthServicesService,
    private _router: Router,
    private flashMessage: FlashMessagesService
  ) { 
    
  }

  ngOnInit() {
        // to make window scroll top
        window.scrollTo(0,0)
  }
  //  on login submit function
  onLoginSubmit() {
    const user = {
      username: this.username,
      password: this.password
    }
    if (this.username == undefined || this.password == undefined) {
      window.scrollTo(0, 0);
      this.flashMessage.show('plz fill  all fields', {cssClass: 'alert-danger', timeout: 3000});
    }else{
      this._authServices.loginUser(user).subscribe((data:any)=> {
        if(data.success) {
          this._authServices.storeUserData(data.token, data.user);
          this._router.navigate(['/users/adminDashboard']);
        } else {
          window.scrollTo(0, 0);
          this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
          this._router.navigate(['/users/admin']);
          }
        }, // end new fromdata to send to server
        (err)=>{
          window.scrollTo(0,0)
          this.errorMessage = err;
        }) // end subscribe
      }
    }  // end login submit function

}
