import { Component, OnInit } from '@angular/core';
import { ImagesServicesService } from '../../services/images-services.service';
import { Router} from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';


@Component({
  selector: 'app-remove-image',
  templateUrl: './remove-image.component.html',
  styleUrls: ['./remove-image.component.scss']
})
export class RemoveImageComponent implements OnInit {
  images:any;
  constructor(
    private _imagesService:ImagesServicesService,
    private _router:Router,
    private flashMessages:FlashMessagesService

  ) { }

  ngOnInit() {
// start get all images
    this._imagesService.getData().subscribe((res:any)=>{
      if (!res.success) {
        this.flashMessages.show(res.errMSG.message, {cssClass: 'alert-danger', timeout: 3000});
      }else{
        this.images = res.images
      }
    },
    (err)=>{
      this.flashMessages.show(err.message, {cssClass: 'alert-danger', timeout: 3000});
    })
  }// end get all images
  
  //start remove image
  remove(id){
    this._imagesService.removeImage(id).subscribe((res:any)=>{
          for (let i = 0; i < this.images.length; i++) {
            const item = this.images[i];        
            if(item.id = id){
                this.images.splice(i, 1);
                this._imagesService.getData().subscribe((data:any)=>{
                  if (!res.success) {
                    window.scrollTo(0,0);
                    this.flashMessages.show(res.errMSG.message, {cssClass: 'alert-danger', timeout: 3000});
                  }else{
                    this.images = data.images;
                    window.scrollTo(0,0);
                    this.flashMessages.show(res.MSG, {cssClass: 'alert-success', timeout: 3000});
                  }
                }) // end get images from server after remove image
                break;
            }else{
              window.scrollTo(0,0);
              this.flashMessages.show(res.message, {cssClass: 'alert-danger', timeout: 3000});
            }
      }
    }, // end remove image from servar
    (err)=>{
      this.flashMessages.show(err.message, {cssClass: 'alert-danger', timeout: 3000});
    }
  )
  }//end remove image

}
