import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError,  } from "rxjs";
import { map, catchError } from 'rxjs/operators';
import { AuthServicesService } from './auth-services.service';
import { GetServicesService } from './get-services.service';
import { Image } from "../modules/image";


@Injectable({
  providedIn: 'root'
})
export class ImagesServicesService {
_url = '/images';
// _url = '/images';

  constructor(
    private _http:HttpClient,
    private _authService:AuthServicesService,
    private _services:GetServicesService
  ) { }

  removeImage(id){
     let  headers = new HttpHeaders({
        'Content-Type':  'application/json',
        'Authorization': this._authService.authToken
      })
      return this._http.get(this._url + '/remove/'+ id)
        .pipe(catchError(this._services.handleError));
  }
// add new image
  addNewImage(formData):Observable<Image>{
    return this._http.post<Image>(this._url, formData)
      .pipe(catchError(this._services.handleError))
  }// end add new image

// get data from url + observable
  getData():Observable<Image>{
    return this._http.get<Image>(this._url)
      .pipe(catchError(this._services.handleError))
  }// end get data from url + observable

}
