import { Component, OnInit } from '@angular/core';
import { ValidateServicesService } from '../../services/validate-services.service';
import { AuthServicesService } from '../../services/auth-services.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  username: String;
  email: String;
  password: String;
  repassword:string;
  errorMessage:string;
  constructor(
    private validateService: ValidateServicesService,
    private _authServices: AuthServicesService,
    private router: Router,
    private flashMessage: FlashMessagesService) { }

  ngOnInit() {
// to make window scroll top
    window.scrollTo(0,0)
  }

  // registry submit function
  onRegisterSubmit() {
    const user = {
      email: this.email,
      username: this.username,
      password: this.password,
      repassword:this.repassword
    }

// validateService
  if(!this.validateService.validateRegister(user)) {
    this.username = '';
    this.password = '';
    this.email = '';
    this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeout: 3000});
    window.scrollTo(0, 0);
    return false;
  } // validateService

// Validate Email
  if(!this.validateService.validateEmail(user.email)) {
    this.username = '';
    this.password = '';
    this.email = '';
    this.flashMessage.show('Please use a valid email', {cssClass: 'alert-danger', timeout: 3000});
      window.scrollTo(0, 0);
      return false;
  }  // end Validate Email

// to equal password field with repasword field 
    if (user.password != user.repassword) {
      this.flashMessage.show('password fied must be the same repassword', {cssClass: 'alert-danger', timeout: 3000});
      this.password = '';
      this,this.repassword = '';
      window.scrollTo(0, 0);
      return false;
    }//end to equal password field with repasword field 

// start  Register
    this._authServices.registerUser(user).subscribe((data:any) => {
    if(data.success) {
      this.router.navigate(['/users/admin']);
    } else {
      this.flashMessage.show(data.sms, {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/users/register']);
      window.scrollTo(0, 0);
    }
  }, // end new fromdata to send to server
  (err)=>{
    window.scrollTo(0,0)
    this.errorMessage = err;
  }) // end subscribe
  
  } // end on registre submit
}
