import { Component, OnInit } from '@angular/core';
import { MessageService } from '../../services/message.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  constructor(
    private _messagesServices:MessageService,
    private flashMessage:FlashMessagesService,
    private _http:HttpClient
  ) { }
  name:string;
  email:string;
  message:string;
  ngOnInit() {
  }
// start send message 
  sendMessage(){
   let msg = {
      name:this.name,
      email:this.email,
      message:this.message
    }
    this._messagesServices.sendMessage(msg).subscribe((res:any)=>{
      if (res.success) {
        window.scrollTo(0, 0);
        this.flashMessage.show(res.MSG, {cssClass: 'alert-success', timeout: 5000});
      }else{
        window.scrollTo(0, 0);
        this.flashMessage.show(res.errMSG, {cssClass: 'alert-danger', timeout: 5000});
      }
    },
    (err)=>{
      window.scrollTo(0,0)
      this.flashMessage.show(err, {cssClass: 'alert-danger', timeout: 5000});

    })
  }// end send message 


}
