import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError,  } from "rxjs";
import { map, catchError } from 'rxjs/operators';
import { Image } from "../modules/image";

@Injectable({
  providedIn: 'root'
})
export class GetServicesService {

  constructor( 
    private _getHttp:HttpClient,
  ) { }
// handel error getData 
  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      return throwError(
        'Something bad happened; please try again later.' + error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(error.message);
  };

// sort data by catgory
    filterByCategory(data, filterBy: string): Image{
      filterBy = filterBy.toLocaleLowerCase();
      return data.filter((images: Image) =>
      images.category.toLocaleLowerCase().indexOf(filterBy) !== -1);
      }    // end filter data by catgory


// to filter images by spiatcal image
  filterBySpCategory(data, filterBy: string): Image{
    filterBy = filterBy.toLocaleLowerCase();
    return data.filter((images: Image) =>
    images.sp_category.toLocaleLowerCase().indexOf(filterBy) !== -1);
    }    // end filter images by spiatcal image


}
