import { Component, OnInit, } from '@angular/core';
import { GetServicesService } from '../../services/get-services.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ImagesServicesService } from '../../services/images-services.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
  images:any = 0;
  category:string;
  errorMessage:any;
  buttons:any;
  
  constructor(
    private _service:GetServicesService,
    private _imagesService:ImagesServicesService,
    private _activeRouter:ActivatedRoute,
    private flashMessage:FlashMessagesService
  ) { }
  ngOnInit() {
// to kep windo scroll top on init
    window.scrollTo(0,0);
// get data from params
    this._activeRouter.paramMap.subscribe((params:ParamMap)=>{
      let category =  params.get('category');
      this._imagesService.getData().subscribe((res:any)=>{
// start switch
        switch (category) {
          case 'animals':
          this.buttons = ['all', 'horses', 'birdes', 'dogs', 'wolfs', 'elefants', 'cats']
          break;
        case 'food':
          this.buttons = ['all', 'drinks', 'spice', 'fast food']
          break;
        case 'art':
          this.buttons = ['all', 'old art', 'grafety', 'free style']
          break;
        case 'natral':
          this.buttons = ['all', 'flowers', 'sea', 'other']
          break;
        case 'cars':
          this.buttons = ['all', 'red', 'blue', 'yellow']
          break;
        case 'town':
          this.buttons = ['all', 'streets', 'bilding', 'houses']
          break;
        } // end switch

        if(category == 'all'){
          this.images = res.images;
          
        } else{
          this.images = this._service.filterByCategory(res.images, category);
        }
    }, err=>{
      this.errorMessage = err;
    }) // end get data from server

    })// end get data from params

  } // end ngOnInit

// open ovar fllow to see image
    seeImage(src, alt){
      var ovar = document.getElementById('ovar');
      var image = document.getElementById('image');
      ovar.classList.add('ovar', 'dis');
      image.setAttribute('src', 'src/'+ src);
      image.setAttribute('alt', 'src/'+ alt);
      document.body.style.overflow = 'hidden';
    } // end open ovar fllow to see image

// close ovar fllow button
  closeImage(){
    var ovar = document.getElementById('ovar');
    ovar.classList.toggle('ovar');
    document.body.style.overflow = 'auto';
   } // end close ovar fllow button
// start filter special category
   filter(word){
    this._imagesService.getData().subscribe((res:any)=>{
        this._activeRouter.paramMap.subscribe((params:ParamMap)=>{
          let category =  params.get('category');
          this._imagesService.getData().subscribe((res:any)=>{
            if(category == 'all'){
              this.images = this._service.filterByCategory(res.images, category);

            } else{
              this.images = this._service.filterByCategory(res.images, category);
              if (word == 'all') {
                this.images = this._service.filterByCategory(res.images, category);
              }else{
                this.images = this._service.filterBySpCategory(this.images, word);        
              } 
            }
        }, err=>{
          this.errorMessage = err;
        }) // end get data from server

        }) // end get data from params

      })// end get data from params

    }// end fillter special

}
