import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/home/home.component';
import { ErorrComponent } from './components/erorr/erorr.component';
import { ViewComponent } from './components/view/view.component';
import { LoginComponent } from './components/login/login.component';
import { AddImageComponent } from './components/add-image/add-image.component';
import { AuthGuardGuard } from './guard/auth-guard.guard';
import { RegisterComponent } from './components/register/register.component';
import { AdminDashboardComponent } from './components/admin-dashboard/admin-dashboard.component';
import { RemoveImageComponent } from './components/remove-image/remove-image.component';
import { ContactComponent } from './components/contact/contact.component';
import { MessagesComponent } from './components/messages/messages.component';


const routes: Routes = [
  {path:'',  redirectTo:'home', pathMatch:'full'},
  {path:'home', component:HomeComponent},
  {path:'view', component:ViewComponent},
  {path:'contact', component:ContactComponent},
  {path:'users/admin', component:LoginComponent},
  {path:'users/adminDashboard', component:AdminDashboardComponent, canActivate:[AuthGuardGuard], children:[
    {path:'', redirectTo:'add_image', pathMatch:'full', canActivate:[AuthGuardGuard]},
    {path:'add_image', component:AddImageComponent, canActivate:[AuthGuardGuard]},
    {path:'remove', component:RemoveImageComponent, canActivate:[AuthGuardGuard]},
    {path:'register', component:RegisterComponent, canActivate:[AuthGuardGuard]},
    {path:'messages', component:MessagesComponent, canActivate:[AuthGuardGuard]}

  ]},
  {path:'**', component:ErorrComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


export const routingComponents = [
  ViewComponent,
  ErorrComponent,
  HomeComponent,
  LoginComponent,
  AddImageComponent, 
  ContactComponent,  
  RegisterComponent,
  AdminDashboardComponent,
  RemoveImageComponent
]
