import { Injectable } from '@angular/core';
import { tokenNotExpired } from 'angular2-jwt';
import { HttpClient, HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, retry, catchError, tap } from 'rxjs/operators';
import { GetServicesService } from './get-services.service';
import { Router } from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class AuthServicesService {
  _url = '/users';
  // _url = '/users';

  authToken:any;
  user:any;
  isDev:Boolean;
  constructor(
    private http:HttpClient,
    private _router:Router,
    private _service:GetServicesService

  ) {
    this.isDev = true;  // Change to false before deployment
  }
// registry new user
  registerUser(user) {
    return this.http.post<any>(this._url +'/register', user)
    .pipe(
      catchError(this._service.handleError)
    )
  } // end registry new user

// login user
  loginUser(user) {
    {
      let headers = new HttpHeaders();
      headers.append('Content-Type', 'application/json');
      return this.http.post(this._url +'/admin', user)
      .pipe(
        catchError(this._service.handleError)
      );
    }
  }// login user

// get item from localstorge token
  getToken() {
    return localStorage.getItem('token')
  }// end localstorge token

// set item from localstorge token
  storeUserData(token, user) {
      localStorage.setItem('token', token);
      localStorage.setItem('user', JSON.stringify(user));
      this.authToken = token;
      this.user = user;
    }// end set item from localstorge token

  // loggedin 
  loggedIn() {
    return tokenNotExpired('token');
  }   // end loggedin 

// start load token 
  loadToken() {
  const token = localStorage.getItem('token');
  this.authToken = token;
  }// end load token 

// to get profile 
  getProfile() {
  this.loadToken();
  let headers = new HttpHeaders({
    'Authorization': this.authToken,
    'Content-Type': 'application/json'
  });
  return this.http.get(this._url +'/add_image', {headers: headers})
        .pipe(
          catchError(this._service.handleError)
        );
  }//end get profile 

// logout
  logout() {
  this.authToken = null;
  this.user = null;
  localStorage.clear();
  } // end logout

}
