import { Component, OnInit, ElementRef } from '@angular/core';
import { ImagesServicesService } from '../../services/images-services.service';
import {  FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { FlashMessagesService } from 'angular2-flash-messages';


const _url = '/images';
@Component({
  selector: 'app-add-image',
  templateUrl: './add-image.component.html',
  styleUrls: ['./add-image.component.scss']
})
export class AddImageComponent implements OnInit {
  // to use fileUploder
  public uploader:FileUploader = new FileUploader({url: _url, itemAlias: 'photo'});
  options:any;
  image:File;
  category:string;
  alt:string;
  sp_category:string;
  selectUpload:string;
  fileToUpload:File = null;
  imageURL:string = "";
  uploadPath:string = '';
  errorMessage:string;

  constructor(
    private _images:ImagesServicesService,
    private element: ElementRef,
    private _http:HttpClient,
    private flashMessage: FlashMessagesService,

  ) { }
  ngOnInit() {

  } // end ngOnInit
 
// add new image
  addImage(){
// to make shor all fields not empty befor send data
    if (this.alt == undefined || this.category == undefined || this.image == undefined || '' || this.sp_category == undefined) {
      this.flashMessage.show('plz fill  all fields', {cssClass: 'alert-danger', timeout: 3000});
      window.scrollTo(0, 0);
    } else{
// locate the file element meant for the file upload.
  let inputEl: HTMLInputElement = this.element.nativeElement.querySelector('#photo');
//create a new fromdata to send to server
    let formData = new FormData();
    formData.append('photo', inputEl.files.item(0))
    formData.append('alt', this.alt)
    formData.append('category', this.category)
    formData.append('sp_category', this.sp_category)

    this._images.addNewImage(formData).subscribe((res:any) => {
      if (res.success) {
        this.flashMessage.show(res.MSG, {cssClass: 'alert-success', timeout: 2000});
      }else{
        window.scrollTo(0, 0);
        this.flashMessage.show(res.errMSG.message, {cssClass: 'alert-danger', timeout: 2000});
      }
    }, // end new fromdata to send to server
    (err)=>{
      window.scrollTo(0,0)
      this.errorMessage = err;
    }) // end subscribe

//to clear filelds value and make window scroll top after send data
    window.scrollTo(0, 0);
        this.alt = null;
        this.category = undefined;
        this.image = undefined;
        this.sp_category = undefined;
        this.imageURL = "";
        this.selectUpload = '';
    } // end else
  } // end add new image

// to change special category filde
  changeOption(){
    switch (this.category) {
      case 'animals':
        this.options = ['horses', 'birdes', 'dogs', 'wolfs', 'elefants', 'cats']
        break;
      case 'food':
        this.options = ['drinks', 'spice', 'fast food', 'fruit']
        break;
      case 'art':
        this.options = ['old art', 'grafety', 'free style']
        break;
      case 'natral':
        this.options = ['flowers', 'sea', 'other']
        break;
      case 'cars':
        this.options = ['red', 'blue', 'yellow']
        break;
      case 'town':
        this.options = ['streets', 'bilding', 'houses']
        break;
      case '':
        this.options = ['must select category']
        break;
      default:
        this.options = ['must select category']
        break;
    }// end switch
  }// end change special category filde

// to see image when selected
  onChange(file:FileList){
    this.fileToUpload = file.item(0);
    this.selectUpload = file.item(0).name
    var redar = new FileReader()
    redar.onload = (event:any)=>{
      this.imageURL = event.target.result;
    }
    redar.readAsDataURL(this.fileToUpload)
  } // end see image when selected


}
